import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-theboys',
  templateUrl: './theboys.component.html',
  styleUrls: ['./theboys.component.scss']
})
export class TheboysComponent implements OnInit {


  vistos = [];
  checksRef = [];

  constructor() { }

  ngOnInit() {
  }

    checkEpisode(i) {
    if (this.checksRef[i].classList.contains("visto")) {
      this.checksRef[i].classList.remove('visto');
      this.vistos = this.vistos.filter(elem => {return elem !== i});
      localStorage.setItem(i, JSON.stringify(this.vistos));
    } else {
      this.checksRef[i].classList.add('visto');
      this.vistos.push(i)
      localStorage.setItem(i, JSON.stringify(this.vistos));
    }
  }

}
